import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';
import './Trail.css';
import FavoriteIcon from '@material-ui/icons/Favorite';
import axios from 'axios';
import Subscribe from './Subscribe';
import LiveUpdate from './LiveUpdate';
import Map from './Map';


const Trail = ({ externalID }) => {
  // const BASE_URL = 'http://127.0.0.1:8000/diamondtrails'
  const BASE_URL = 'https://trailmix-base.wm.r.appspot.com/diamondtrails'

  const emptyStatus = [{'message': 'SHARE LIVE STATUS', 'created_at': ''}]

  const [ trailData, setTrailData ] = useState({});
  const [ parkingStats, setParkingStats ] = useState(emptyStatus);
  const [ visitorStats, setVisitorStats ] = useState(emptyStatus);
  const [ weatherStats, setWeatherStats ] = useState(emptyStatus);
  const [ subscriptions, setSubscriptions ] = useState(0);
  const [ refresh, setRefresh ] = useState(0);


  // Make API call when componentDidMount and page refreshed (any status updated)
  useEffect(() => {
    axios
    .get(`${BASE_URL}/trail/${externalID}`)
    .then((response) => {
      // console.log(response.data.trails[0]);
      // console.log('JOYFUL tears! Received data from all these layers')
      // console.log(response.data.weather_updates);
      const newTrailData = response.data.trails[0];
      setTrailData(newTrailData);
      setSubscriptions(response.data.subscriptions);
      setWeatherStats(response.data.weather_updates);
      setParkingStats(response.data.parking_updates);
      setVisitorStats(response.data.visitor_updates);

    })
    .catch((error) => {
      console.log(error.message);
    })
  }, [refresh, externalID]);


  const count_refresh = () => {
    const newRefresh = refresh + 1;
    setRefresh(newRefresh);
  }

  const weather_stat = () => {
    let stats = emptyStatus[0].message;
    if ( weatherStats.length !== 0 ) {
      stats = weatherStats[0].message;
    } 

    return stats;
  }

  const weather_timestamp = () => {
    let stats = emptyStatus[0].created_at;
    if ( weatherStats.length !== 0 ) {
      const timestamp = weatherStats[0].created_at;
      const dateObj = new Date(timestamp);
      stats = dateObj.toLocaleString();
    } 

    return stats;
  }

  const parking_stat = () => {
    let stats = emptyStatus[0].message;
    if ( parkingStats.length !== 0 ) {
      stats = parkingStats[0].message;
    } 

    return stats;
  }

  const parking_timestamp = () => {
    let stats = emptyStatus[0].created_at;
    if ( parkingStats.length !== 0 ) {
      const timestamp = parkingStats[0].created_at;
      const dateObj = new Date(timestamp);
      stats = dateObj.toLocaleString();
    } 

    return stats;
  }

  const visitor_stat = () => {
    let stats = emptyStatus[0].message;
    if ( visitorStats.length !== 0 ) {
      stats = visitorStats[0].message;
    } 

    return stats;
  }

  const visitor_timestamp = () => {
    let stats = emptyStatus[0].created_at;
    if ( visitorStats.length !== 0 ) {
      const timestamp = visitorStats[0].created_at;
      const dateObj = new Date(timestamp);
      stats = dateObj.toLocaleString();
    } 

    return stats;
  }


  return (
    <div className="container trail-main-container text-center">

      <div className="card">
        <img className="card-img trail-img shadow" src={trailData.imgMedium} alt={trailData.name} />
        <div className="card-img-overlay">
          <p className="card-text"></p>
        </div>
      </div>

      <section>
        <h1>{ trailData.name }</h1>
        <h4> 
        <span className="text-muted" > {trailData.location} </span> 
        {/* <span> {trailData.length} mi </span> */}
      </h4>
      </section>


      <div>
        <FavoriteIcon color="secondary" />
        <h5>  {subscriptions} subscribed within past 3 days </h5>
      </div>


      <Subscribe trail={trailData} refresh={count_refresh} />
      <LiveUpdate trail={trailData} refresh={count_refresh} />

    
      <div className="card-deck">
        <div className="card border-0 shadow">
          <img className="card-img card-img-top" src="https://lh3.googleusercontent.com/pw/ACtC-3cnK49FbkKIBreGnKxXexLYxK_Ta57aITBbQE4yYtRyV3foDMd9HyGBpUouADLSY4Q0dda1BzXbAhyUSFSoVTu7WjDpcCl5Drl9G2ndLkD-M3xiYW-PzMnjINXnr5erFkWNK3b9VqlQJneARaytSH2FgA=w1692-h1128-no?authuser=0" alt="Car infront of Mountain by Makenzie Cooper from Unsplash" />
          <div className="card-img-overlay text-center text-light">
            <h3> Parking </h3>
          </div>
          <div className="card-body">
            <h5 className="card-text"> <span role="img" aria-labelledby="parking"> üÖøÔ∏è </span> Parking {parking_stat()} </h5>
            <span className="card-link"> {parking_timestamp()} </span>
          </div>
        </div>

        <div className="card border-0 shadow">
          <img className="card-img card-img-top" src="https://lh3.googleusercontent.com/pw/ACtC-3f_QJYX55Fplkf43FB_1lXkjVLOj879wdVAiqOJiXkoUaP1fw_suexKflTRns3kQO1KRVj3jezRQT5V9JeutIc8UHRqRrFG9IiToFRoounFyIWAqJaQ1_40R6fvQ2BNczZ12YGQILNUxM6AzXn98xuvQg=w1840-h1226-no?authuser=0" alt="Hikers in mountains by Ashim-d-Silva from Unsplash" />
          <div className="card-img-overlay text-center text-light">
            <h3> Visitors </h3>
          </div>
          <div className="card-body">
            <h5 className="card-text"> <span role="img" aria-labelledby="hiker boot"> ü•æ</span> {visitor_stat()} </h5>
            <p className="card-link"> {visitor_timestamp()} </p>
          </div>
        </div>

        <div className="card border-0 shadow">
          <img className="card-img card-img-top" src="https://lh3.googleusercontent.com/pw/ACtC-3e6hm4nmqR9HWG4fPUvAyeWoSo34lYA9J0vv01VgLPbI4zyvEZ-CESUVZnycYH-Xv6_M9MXnGFRPVZFPM_IFqY6ExQPKlM-2bGW9FOzuYEzevXsKz97QIbEg39i4zPXueR1GQk_jVD3uYtODbqBt8X8ZQ=w1840-h1226-no?authuser=0" alt="Rainbow in mountains by Dan Meyers from Unsplash" />
          <div className="card-img-overlay text-center text-light">
            <h3> Weather </h3>
          </div>
          <div className="card-body">
            <h5 className="card-text"> <span role="img" aria-labelledby="weather"> üå°Ô∏è </span>  {weather_stat()} </h5>
            <p className="card-link"> {weather_timestamp()} </p>
          </div>
        </div>
      </div>

      { trailData.conditionStatus === 'Unknown' ? "" :
      <div className="card shadow">
        <div className="card-header">
          <h5> <span role="img" aria-labelledby="warning in construction"> üöß </span> Other Conditions Update </h5>
        </div>
        <div className="card-body">
          <span className="card-link mb-2"> {trailData.conditionStatus} </span>
          <span className="card-link mb-2"> {trailData.conditionDetails} </span>
          <span className="card-link mb-2"> {trailData.conditionDate} </span>
        </div>
      </div>
      }

      <div className="card shadow">
        <div className="card-body">
          <h4 className="card-title">  {trailData.name} </h4>
          
          <p className="card-text mb-2 text-muted"> {trailData.summary} </p>
          <p className="card-text mb-2"> {trailData.location} </p>
          <p className="card-text mb-2"> {trailData.length} Miles </p>
          <p className="card-text mb-2 "> {trailData.ascent}' Up </p>
          <p className="card-text mb-2 "> {trailData.descent}' Down </p>
        </div>
      </div>

      <Map lat={trailData.latitude} lng={trailData.longitude} />

    </div>
  )
}


Trail.propTypes = {
  externalID: PropTypes.number.isRequired,
};

export default Trail;